<template>
  <div style="width: 600px; padding: 5px 0;">
    <p class="title" style="margin-bottom: 10px; font-size: 20px;">
      技术极致能耗 kgce/t-s
      <!-- <button @click="submitToStore()">提交</button> -->
    </p>
    <div class="container">
      <div class="left">
        <ul style="padding-left: 5px; font-size: 16px; margin-bottom: 5px;">
          <li v-for="item in consumptionItems" :key="item.index" style="list-style: none; margin-bottom: 5px;">
            <span style="width: 120px; display: inline-block; line-height: 30px;">{{ item.label }}</span>
            <input type="text" v-model="item.text" class="narrow-input" />
          </li>
        </ul>
      </div>
      <div class="right">
        <ul style="padding-left: 5px; font-size: 16px; margin-bottom: 5px;">
          <li v-for="item in coefficientItems" :key="item.index" style="list-style: none; margin-bottom: 5px;">
            <span style="width: 120px; display: inline-block; line-height: 30px;">{{ item.label }}</span>
            <input type="text" v-model="item.text" class="narrow-input" />
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
  
<script>
import { EventBus } from '@/EventBus.js';
import axios from 'axios';
export default {
  name: 'techConsumption',
  data() {
    return {
      consume: 50.6,
      consumptionItems: [
        { label: '一次配水比', text: '', backendName: 'H2O_1' },
        { label: '二次配水比', text: '', backendName: 'H2O_2' },
        { label: '抽风过剩系数', text: '', backendName: 'CoffExcess1' },
        { label: '点火能耗', text: '', backendName: 'Q点火' },
        { label: '原料温度', text: '', backendName: 'TMaterial' },
        { label: '烧结矿温度', text: '', backendName: 'TSinter' },
        { label: '返矿率', unit: 'kg', text: '', backendName: 'ReturnSinter' }
      ],
      coefficientItems: [
        { label: '热损失', text: '', backendName: 'R_LOSS0' },
        { label: '漏风率', text: '', backendName: 'Loss_air' },
        { label: '烧结矿FeO', text: '', backendName: 'TFeSinter' },
        { label: '烧结矿碱度', text: '', backendName: 'R2Sinter' },
        { label: '烟气温度', text: '', backendName: 'TGas' },
        { label: '烟气中CO比例', text: '', backendName: 'RCOGas' },
        { label: '余热回收率', text: '', backendName: 'R_Rechot' }
      ]
    }
  },
  methods: {
    /**
    * 将后端获取的结果（backendDataArr）赋值到第一个参数（elementArr）里
    * 如果elementArr的数据项内，存在backendName属性，则按照backendName匹配
    * 如果不存在，则按照label匹配
    * @param {*} elementArr 画面变量
    * @param {*} backendDataArr 后端获取到的数据
    */
    searchInBackendReturnData(elementArr, backendDataArr) {
      for (let j = 0; j < elementArr.length; j++) {
        const element = elementArr[j];
        for (let index = 0; index < backendDataArr.length; index++) {
          let flag = 0;
          if (element.backendName != undefined) {
            // console.log(element);
            flag = backendDataArr[index].name == element.backendName
          }
          else {
            flag = backendDataArr[index].annotation.match(element.label)
          }
          if (flag) {
            element.text = backendDataArr[index].parameter
            break
          }
        }
      }
    },
    submitToStore() {
      console.log('in procedure tech, submit to store');
      function searchInItems(items, backendName) {
        for (let i = 0; i < items.length; i++) {
          const element = items[i];
          if (element.backendName == backendName) {
            // console.log(element.backendName, backendName, element.text);
            return Number(element.text);

          }
        }

      }

      let md = this.$store.state.materialData;
      md.param_ = {}
      md.param_.H2O_1 = searchInItems(this.consumptionItems, "H2O_1");
      md.param_.H2O_2 = searchInItems(this.consumptionItems, "H2O_2");
      md.param_.CoffExcess1 = searchInItems(this.consumptionItems, "CoffExcess1");
      md.param_.QFire = searchInItems(this.consumptionItems, "Q点火");
      md.param_.TMaterial = searchInItems(this.consumptionItems, "TMaterial");
      md.param_.TSinter = searchInItems(this.consumptionItems, "TSinter");
      md.param_.ReturnSinter = searchInItems(this.consumptionItems, "ReturnSinter");
      md.param_.R_Loss0 = searchInItems(this.coefficientItems, "R_LOSS0");
      md.param_.Loss_air = searchInItems(this.coefficientItems, "Loss_air");
      md.param_.TFeSinter = searchInItems(this.coefficientItems, "TFeSinter");
      md.param_.R2Sinter = searchInItems(this.coefficientItems, "R2Sinter");
      md.param_.TGas = searchInItems(this.coefficientItems, "TGas");
      md.param_.tttt = searchInItems(this.coefficientItems, "tttt");
      md.param_.R_Rechot = searchInItems(this.coefficientItems, "R_Rechot");

      console.log('in procedure tech, materialData', md);
      this.$store.commit("setMaterialData", md);
      this.$router.push("energy-efficiency")
    },


  },
  // mouned(){
  //     for (const key in this.techParams) {
  //         if (Object.hasOwnProperty.call(this.techParams, key)) {
  //             const element = this.techParams[key];
  //             console.log(key, element);
  //             this.params.key = element;

  //         }
  //     }
  // },
  mounted() {
    EventBus.$on('submitToStore', () => { // 监听自定义事件
      this.submitToStore()
    })
  },
  created() {
    console.log('in procedure tech created');
    axios.post('/Actual/getActualParameter')
      .then(response => {
        // 处理请求成功的响应
        this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值

        this.searchInBackendReturnData(this.coefficientItems, response.data)
        this.searchInBackendReturnData(this.consumptionItems, response.data)

      })
      .catch(error => {
        // 处理请求失败的错误
        console.error(error);
      });
  },
  beforeDestroy() {
    // 在离开页面的时候取消监听事件，否则二次进页面会导致事件被多次触发
    EventBus.$off('submitToStore')
  }
}

</script>
  
