<template>
  <div style="width: 1500px; padding: 0;">
    <div class="tt">
      <p class="title">通用热力学参数<button @click="submitToStore()">修改</button></p>
    </div>
    <div class="container">
      <div class="left">
        <ul style="padding-left: 40px;padding-top: 20px; font-size: 18px; margin-bottom: 5px;">
          <li v-for="item in filteredItemsEven" :key="item.index" style="list-style: none; margin-bottom: 22px;">
            <span style="width: 200px; display: inline-block; line-height: 30px;">{{ item.label }}</span>
            <input type="text" v-model="item.additionalText" class="narrow-input" />
            <span> {{ item.unit }}</span>
          </li>
        </ul>
      </div>
      <div class="right">
        <ul style="padding-left: 40px;padding-top: 20px; font-size: 18px; margin-bottom: 5px;">
          <li v-for="item in filteredItemsOdd" :key="item.index" style="list-style: none; margin-bottom: 22px;">
            <span style="width: 200px; display: inline-block; line-height: 30px;">{{ item.label }}</span>
            <input type="text" v-model="item.additionalText" class="narrow-input" />
            <span> {{ item.unit }}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
export default {
  name: 'actualConsumption',
  data() {
    return {
      consumptionItems: [
        { label: '混合料的平均比热容：', additionalText: "", unit: "kJ/kg·℃", backendName: "CMixed" },
        { label: '铺底料的平均比热容：', additionalText: "", unit: "kJ/kg·℃", backendName: "CBeddingLayer" },
        { label: '水的平均比热容：', additionalText: "", unit: "kJ/kg·℃", backendName: "CWater" },
        { label: '烟气的平均比热容：', additionalText: "", unit: "kJ/kg", backendName: "CFlue_gas" },
        { label: '空气的平均比热容：', additionalText: "", unit: "kJ/kg", backendName: "CAir" },
        { label: '煤气的平均比热容：', additionalText: "", unit: "kJ/kg", backendName: "CGas" },
        { label: '生成1kg FeS2 吸热：', additionalText: "", unit: "kJ/kg", backendName: "qFeS2" },
        { label: '生成1kg FeO 吸热：', additionalText: "", unit: "kJ/kg", backendName: "qFeO" },
        { label: '生成1kg H20吸热：', additionalText: "", unit: "kJ/kg", backendName: "qWater" },
        { label: '生成 1kg Cao吸热：', additionalText: "", unit: "kJ/kg", backendName: "qCaO" },
        { label: '生成 1kg Mgo吸热：', additionalText: "", unit: "kJ/kg", backendName: "qMgO" },
        { label: '空气密度：', additionalText: "", unit: "kg/m³", backendName: "pAir" },
        { label: '氧气密度：', additionalText: "", unit: "kg/m³", backendName: "pO2" },
      ],
    };
  },
  computed: {
    filteredItemsEven() {
      return this.consumptionItems.filter((item, index) => index % 2 === 0);
    },
    filteredItemsOdd() {
      return this.consumptionItems.filter((item, index) => index % 2 !== 0);
    }
  },
  methods: {
    /**
     * 将后端获取的结果（backendDataArr）赋值到第一个参数（elementArr）里
     * 如果elementArr的数据项内，存在backendName属性，则按照backendName匹配
     * 如果不存在，则按照label匹配
     * @param {*} elementArr 画面变量
     * @param {*} backendDataArr 后端获取到的数据
     */
    searchInBackendReturnData(elementArr, backendDataArr) {
      for (let j = 0; j < elementArr.length; j++) {
        const element = elementArr[j];
        for (let index = 0; index < backendDataArr.length; index++) {
          let flag = 0;
          if (element.backendName != undefined) {
            // console.log(element);
            flag = backendDataArr[index].name == element.backendName
          }
          else {
            flag = backendDataArr[index].annotation.match(element.label)
          }
          if (flag) {
            element.additionalText = backendDataArr[index].value
            break
          }
        }
      }
    },
    submitToStore() {
      function searchInItems(items, backendName) {
        for (let i = 0; i < items.length; i++) {
          const element = items[i];
          if (element.backendName == backendName) {
            // console.log(element.backendName, backendName, element.text);
            // console.log(element);
            return Number(element.additionalText);

          }
        }

      }

      let md = this.$store.state.materialData;
      console.log(md);
      // md.param.C混合料 = searchInItems(this.consumptionItems, "C混合料");
      // md.param.C铺底料 = searchInItems(this.consumptionItems, "C铺底料");
      // md.param.C水 = searchInItems(this.consumptionItems, "C水");
      // md.param.C烟气 = searchInItems(this.consumptionItems, "C烟气");
      // md.param.C空气 = searchInItems(this.consumptionItems, "C空气");
      // md.param.C煤气 = searchInItems(this.consumptionItems, "C煤气");
      // md.param.qFeS2 = searchInItems(this.consumptionItems, "qFeS2");
      // md.param.qFeO = searchInItems(this.coefficientItems, "qFeO");
      // md.param.q水 = searchInItems(this.coefficientItems, "q水");
      // md.param.qCaO = searchInItems(this.coefficientItems, "qCaO");
      // md.param.qMgO = searchInItems(this.coefficientItems, "qMgO");
      // md.param.p空气 = searchInItems(this.coefficientItems, "p空气");
      // md.param.pO2 = searchInItems(this.coefficientItems, "pO2");
      // console.log(searchInItems(this.consumptionItems, "CMixed"));
      md.paramThermList[0].parameter = searchInItems(this.consumptionItems, "CMixed");
      md.paramThermList[1].parameter = searchInItems(this.consumptionItems, "CBeddingLayer");
      md.paramThermList[2].parameter = searchInItems(this.consumptionItems, "CWater");
      md.paramThermList[3].parameter = searchInItems(this.consumptionItems, "CFlue_gas");
      md.paramThermList[4].parameter = searchInItems(this.consumptionItems, "CAir");
      md.paramThermList[5].parameter = searchInItems(this.consumptionItems, "CGas");
      md.paramThermList[6].parameter = searchInItems(this.consumptionItems, "qFeS2");
      md.paramThermList[7].parameter = searchInItems(this.consumptionItems, "qFeO");
      md.paramThermList[8].parameter = searchInItems(this.consumptionItems, "qWater");
      md.paramThermList[9].parameter = searchInItems(this.consumptionItems, "qCaO");
      md.paramThermList[10].parameter = searchInItems(this.consumptionItems, "qMgO");
      md.paramThermList[11].parameter = searchInItems(this.consumptionItems, "pAir");
      md.paramThermList[12].parameter = searchInItems(this.consumptionItems, "pO2");








      this.$store.commit("setMaterialData", md);
      this.$router.push("energy-efficiency")


      // axios.post('/Actual/GetActualVo')
      //     .then(response => {
      //         // 处理请求成功的响应
      //         this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值

      //         this.searchInBackendReturnData(this.coefficientItems, response.data)
      //         this.searchInBackendReturnData(this.consumptionItems, response.data)

      //     })
      //     .catch(error => {
      //         // 处理请求失败的错误
      //         console.error(error);
      //     });
    }

  },
  created() {
    axios.post('http://localhost:8888/RatioMixed/getParamTherm')
      .then(response => {
        // 处理请求成功的响应
        this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值
        this.searchInBackendReturnData(this.consumptionItems, response.data)

      })
      .catch(error => {
        // 处理请求失败的错误
        console.error(error);
      });
  }
}
</script>

