    <div style="width: 600px; padding: 5px 0;">
        <!-- ... 其他代码 ... -->
        <p class="title" style="margin-bottom: 10px; font-size: 20px;">理论极致能耗 kgce/t-s </p>
        <!-- <button @click="submitToStore()">提交</button></p> -->
        <div class="container">
            <div class="left">
                <ul style="padding-left: 5px; font-size: 16px; margin-bottom: 5px;">
                    <li v-for="(item, index) in consumptionItems" :key="index"
                        style="list-style: none; margin-bottom: 5px;">
                        <span style="width: 110px; display: inline-block; line-height: 30px;">{{ item.label }}</span>
                        <input type="text" v-model="item.text" class="input-field" />
                    </li>
                </ul>
            </div>
            <div class="right">
                <ul style="padding-left: 5px; font-size: 16px; margin-bottom: 5px;">
                    <li v-for="(item, index) in coefficientItems" :key="index"
                        style="list-style: none; margin-bottom: 5px;">
                        <span style="width: 110px; display: inline-block; line-height: 30px;">{{ item.label }}</span>
                        <input type="text" v-model="coefficientItems[index].text" class="input-field" />
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    // ... 其他代码 ...
    data() {
        return ({
            consume: 50.6, // 假设从后端获取的数据为 50.6
            consumptionItems: [//返回后端TFe烧结矿，Tgas_end烟气温度，R2烧结矿碱度，Tsolid_end烧结矿温度，Tsolid_start原料温度，Tgas_start空气温度
                { label: '烧结矿品位', text: '', backendName: "TFeSinter" },
                { label: '烧结矿碱度', text: '', backendName: "R2Sinter" },
                { label: '原料温度', text: '', backendName: "TMaterial" },

            ],
            coefficientItems: [
                { label: '烧结矿温度', text: '', backendName: "TSinter" },
                { label: '烟气温度', text: '', backendName: "TGas" },
                { label: '空气温度', text: '', backendName: "t抽风" },
            ]
        })
    },
    methods: {
        searchInBackendReturnData(elementArr, backendDataArr) {
            for (let j = 0; j < elementArr.length; j++) {
                const element = elementArr[j];
                for (let index = 0; index < backendDataArr.length; index++) {
                    let flag = 0;
                    if (element.backendName != undefined) {
                        // console.log(element);
                        flag = backendDataArr[index].name == element.backendName
                    }
                    else {
                        flag = backendDataArr[index].annotation.match(element.label)
                    }
                    if (flag) {
                        element.text = backendDataArr[index].parameter
                        break
                    }
                }
            }
        },
        submitToStore() {
            function searchInItems(items, backendName) {
                for (let i = 0; i < items.length; i++) {
                    const element = items[i];
                    if (element.backendName == backendName) {
                        // console.log(element.backendName, backendName, element.text);
                        return Number(element.text);

                    }
                }

            }

            let md = this.$store.state.theoryData;
            // console.log(md);
            md.TFe = searchInItems(this.consumptionItems, "TFe");
            md.Tgas_end = searchInItems(this.coefficientItems, "Tgas_end");
            md.R2 = searchInItems(this.consumptionItems, "R2");
            md.Tsolid_end = searchInItems(this.coefficientItems, "Tsolid_end");
            md.Tsolid_start = searchInItems(this.consumptionItems, "Tsolid_start");
            md.Tgas_start = searchInItems(this.coefficientItems, "Tgas_start");
            // md.ReturnSinter = searchInItems(this.consumptionItems, "ReturnSinter");
            console.log('theoryData', md);
            this.$store.commit("setTheoryData", md);
            this.$router.push("energy-efficiency")


            // axios.post('/Actual/GetActualVo')
            //     .then(response => {
            //         // 处理请求成功的响应
            //         this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值

            //         this.searchInBackendReturnData(this.coefficientItems, response.data)
            //         this.searchInBackendReturnData(this.consumptionItems, response.data)

            //     })
            //     .catch(error => {
            //         // 处理请求失败的错误
            //         console.error(error);
            //     });
        }


    },
    created() {



        axios.post('/Actual/getActualParameter')
            .then(response => {
                // 处理请求成功的响应
                this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值

                this.searchInBackendReturnData(this.coefficientItems, response.data)
                this.searchInBackendReturnData(this.consumptionItems, response.data)
                console.log(this.coefficientItems);

            })
            .catch(error => {
                // 处理请求失败的错误
                console.error(error);
            });
    }
}
</script>

