<template>
  <div class="main-container " style="width: 600px; padding: 5px 0;"> <button @click="submitToStore()"
      class="submitbn">设置</button>
    <p class="title">实际工序能耗 kgce/t-s</p>
    <div class="container">
      <div class="left">
        <ul>
          <li v-for="item in consumptionItems" :key="item.index">
            <span>{{ item.label }}</span>
            <input type="text" v-model="item.text" class="narrow-input" />
          </li>
        </ul>
      </div>
      <div class="right">
        <ul>
          <li v-for="item in coefficientItems" :key="item.index">
            <span>{{ item.label }}</span>
            <input type="text" v-model="item.text" class="narrow-input" />
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
  
<script>
import { EventBus } from '@/EventBus.js'
import axios from 'axios';
export default {
  name: 'actualConsumption',
  props: ['actualParams'],
  data() {
    return {
      consume: 50.6,
      consumptionItems: [
        { label: '一次配水比', text: '', backendName: "H2O_1" },
        { label: '二次配水比', text: '', backendName: "H2O_2" },
        { label: '抽风过剩系数', text: '', backendName: "CoffExcess1" },
        { label: '点火能耗', text: '', backendName: "Q点火" },
        { label: '原料温度', text: '', backendName: "TMaterial" },
        { label: '烧结矿温度', text: '', backendName: "TSinter" },
        { label: '返矿率', unit: 'kg', text: '', backendName: "ReturnSinter" }
      ],
      coefficientItems: [
        { label: '热损失', text: '', backendName: "R_LOSS0" },
        { label: '漏风率', text: this.data9, backendName: "Loss_air" },
        { label: '烧结矿FeO', text: this.data1, backendName: 'TFeSinter' },
        { label: '烧结矿碱度', text: this.data3, backendName: "R2Sinter" },
        { label: '烟气温度', text: this.data15, backendName: "TGas" },
        { label: '烟气中CO比例', text: this.data16, backendName: "RCOGas" },
        { label: '余热回收率', text: this.data17, backendName: "R_Rechot" }
      ]
    };
  },
  methods: {

    /**
     * 将后端获取的结果（backendDataArr）赋值到第一个参数（elementArr）里
     * 如果elementArr的数据项内，存在backendName属性，则按照backendName匹配
     * 如果不存在，则按照label匹配
     * @param {*} elementArr 画面变量
     * @param {*} backendDataArr 后端获取到的数据
     */
    searchInBackendReturnData(elementArr, backendDataArr) {
      for (let j = 0; j < elementArr.length; j++) {
        const element = elementArr[j];
        for (let index = 0; index < backendDataArr.length; index++) {
          let flag = 0;
          if (element.backendName != undefined) {
            // console.log(element);
            flag = backendDataArr[index].name == element.backendName
          }
          else {
            flag = backendDataArr[index].annotation.match(element.label)
          }
          if (flag) {
            element.text = backendDataArr[index].parameter
            break
          }
        }
      }
    },
    submitToStore() {
      this.callSubmitToStore();
      function searchInItems(items, backendName) {
        for (let i = 0; i < items.length; i++) {
          const element = items[i];
          if (element.backendName == backendName) {
            // console.log(element.backendName, backendName, element.text);
            return Number(element.text);

          }
        }

      }

      let md = this.$store.state.materialData;
      // console.log(md);
      md.param.H2O_1 = searchInItems(this.consumptionItems, "H2O_1");
      md.param.H2O_2 = searchInItems(this.consumptionItems, "H2O_2");
      md.param.CoffExcess1 = searchInItems(this.consumptionItems, "CoffExcess1");
      md.param.QFire = searchInItems(this.consumptionItems, "Q点火");
      md.param.TMaterial = searchInItems(this.consumptionItems, "TMaterial");
      md.param.TSinter = searchInItems(this.consumptionItems, "TSinter");
      md.param.ReturnSinter = searchInItems(this.consumptionItems, "ReturnSinter");
      md.param.R_Loss0 = searchInItems(this.coefficientItems, "R_LOSS0");
      md.param.Loss_air = searchInItems(this.coefficientItems, "Loss_air");
      md.param.TFeSinter = searchInItems(this.coefficientItems, "TFeSinter");
      md.param.R2Sinter = searchInItems(this.coefficientItems, "R2Sinter");
      md.param.TGas = searchInItems(this.coefficientItems, "TGas");
      md.param.tttt = searchInItems(this.coefficientItems, "tttt");
      md.param.R_Rechot = searchInItems(this.coefficientItems, "R_Rechot");

      // md.paramThermList[0] = 123;

      this.$store.commit("setMaterialData", md);
      // this.$router.push("energy-efficiency")


      // axios.post('/Actual/GetActualVo')
      //     .then(response => {
      //         // 处理请求成功的响应
      //         this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值

      //         this.searchInBackendReturnData(this.coefficientItems, response.data)
      //         this.searchInBackendReturnData(this.consumptionItems, response.data)

      //     })
      //     .catch(error => {
      //         // 处理请求失败的错误
      //         console.error(error);
      //     });
    },
    callSubmitToStore() {
      EventBus.$emit('submitToStore') // 触发自定义事件
    }
  },

  // mouned(){
  //     for (const key in this.actualParams) {
  //         if (Object.hasOwnProperty.call(this.actualParams, key)) {
  //             const element = this.actualParams[key];
  //             console.log(key, element);
  //             this.params.key = element;

  //         }
  //     }
  // },

  created() {



    axios.post('/Actual/getActualParameter')
      .then(response => {
        // 处理请求成功的响应
        this.flag = response.data.flag; // 根据API返回的数据结构更新data1的值

        this.searchInBackendReturnData(this.coefficientItems, response.data)
        this.searchInBackendReturnData(this.consumptionItems, response.data)

      })
      .catch(error => {
        // 处理请求失败的错误
        console.error(error);
      });
  }
}
</script>
  
